.events__heading {
  line-height: 14px;
  letter-spacing: 0.5em;
  padding: 50px 0; }

.events__images {
  display: flex; }

.events__weddnes,
.events__corporate {
  flex: 1;
  position: relative;
  overflow: hidden; }
  .events__weddnes::before,
  .events__corporate::before {
    transform: translateY(50%);
    position: absolute;
    content: "";
    width: 210px;
    height: 210px;
    border: 20px solid #E8C300;
    z-index: 2;
    transition: all ease-in-out .5s; }

.events__weddnes::before {
  left: 0; }

.events__corporate::before {
  right: 0; }

.corporate__hover-active::before {
  right: -100%; }

.weddnes__hover-active::before {
  left: -100%; }

.events__corporate-image,
.events__weddnes-image {
  background-image: url("../image/events-corporate.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 475px;
  height: 475px;
  margin: 0 auto;
  overflow: hidden; }
  .events__corporate-image:hover .events__corporate-hover,
  .events__weddnes-image:hover .events__corporate-hover {
    transform: translateY(0); }
  .events__corporate-image:hover .events__weddnes-hover,
  .events__weddnes-image:hover .events__weddnes-hover {
    transform: translateY(0); }

.events__corporate-text,
.events__weddnes-text {
  position: absolute;
  background: red;
  top: 0;
  bottom: 0;
  writing-mode: vertical-rl;
  font-family: 'Banny Round';
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 12px;
  letter-spacing: 0em;
  text-align: left;
  padding: 30px;
  background: #000000; }

.events__corporate-text {
  transform: rotate(180deg);
  left: 0; }

.events__weddnes-text {
  right: 0; }

.events__corporate-hover,
.events__weddnes-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: .5s;
  transform: translateY(-100%); }

.events__corporate-button,
.events__weddnes-button {
  font-size: 24px;
  margin: auto;
  transform: translateY(200px);
  border-radius: 5px;
  color: #FFFFFF;
  background: #E8C300;
  padding: 8px;
  cursor: pointer; }
  .events__corporate-button:hover,
  .events__weddnes-button:hover {
    color: #000000; }

.events__description {
